import React from "react";
import { Collapse, Navbar, NavbarToggler, Nav, NavItem } from "reactstrap";
import { Link } from "react-router-dom";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import FileDownloadOutlinedIcon from "@mui/icons-material/FileDownloadOutlined";
import Img from "../styles/images/bek1.jpg";
import Resume from "../styles/resume/nurbekRasulov.pdf";
import ReactTooltip from "react-tooltip";

export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false,
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  }

  //avatar
  render() {
    return (
      <>
        <Navbar expand="md">
          <Link to="/" className="navbar-brand">
            <b className="anim" data-text="𝑹𝒂𝒔𝒖𝒍𝒐𝒗.𝑵">
              𝑹𝒂𝒔𝒖𝒍𝒐𝒗.<b className="N">𝑵</b>
            </b>
          </Link>

          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav navbar>
              <NavItem>
                <Link to="/">𝑯𝒐𝒎𝒆</Link>
              </NavItem>
              <NavItem>
                <Link to="/portfolio">𝑷𝒐𝒓𝒕𝒇𝒐𝒍𝒊𝒐</Link>
              </NavItem>
              <NavItem>
                <Link to="/setup">𝑺𝒆𝒕𝒖𝒑</Link>
              </NavItem>
              <NavItem>
                <Link to="/about">𝑨𝒃𝒐𝒖𝒕 𝑴𝒆</Link>
              </NavItem>
              <NavItem>
                <Link to="/contact">𝑪𝒐𝒏𝒕𝒂𝒄𝒕</Link>
              </NavItem>
              <NavItem>
                <a href={Resume} download="nurbekRasulov.pdf">
                  <ReactTooltip textColor="yellow" />
                  <FileDownloadOutlinedIcon
                    className="resumeColor"
                    data-tip="Resume"
                  />
                </a>
              </NavItem>
              <Stack direction="row" spacing={2}>
                <Avatar alt="Remy Sharp" src={Img} />
              </Stack>
            </Nav>
          </Collapse>
        </Navbar>
      </>
    );
  }
}
