import React from "react";
import NavSec from "../home/NavSec";
import Reveal from "react-reveal/Reveal";
import PageInfo from "../setup/PageInfo";
import SetupMain from "./SatupMain";
import Footer from "../footer/Footer";

const Setup = () => {
  return (
    <>
      <Reveal effect="animNav" dyration="750">
        <NavSec />
      </Reveal>
      <Reveal effect="fadeUp" duration="1250">
        <PageInfo />
      </Reveal>
      <SetupMain />
      <Footer />
    </>
  );
};

export default Setup;
