import React, { useState, useEffect } from "react";
import db from "../firebase/config";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import Img from "../styles/images/n.jpg";

import YouTubeIcon from "@material-ui/icons/Facebook";
import GitHubIcon from "@material-ui/icons/GitHub";
import TelegramIcon from "@material-ui/icons/Telegram";
import InstagramIcon from "@material-ui/icons/Instagram";
import YouTube from "@material-ui/icons/YouTube";
import Button from "@material-ui/core/Button";
import Icon from "@material-ui/core/Icon";

//input
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
//

const Contact = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const [loader, setLoader] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoader(true);

    db.collection("contacts")
      .add({
        name: name,
        email: email,
        message: message,
      })
      .then(() => {
        setLoader(false);
        alert(
          "Your message has been submitted / Sizning xabaringiz yuborildi👍"
        );
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setName("");
    setEmail("");
    setMessage("");
  };

  // inputs
  const useStyles = makeStyles((theme) => ({
    root: {
      "& > *": {
        margin: theme.spacing(1),
        width: "25ch",
      },
    },
  }));
  //
  return (
    <div className="contactMain">
      <Card className="card container">
        <div className="images"></div>
        <div className="col">
          <Typography component="div" variant="h4">
            Get In Touch
          </Typography>
          <div className="socialNet">
            <a
              className="links"
              href="https://www.facebook.com/nurbek.rasulov.370"
              target={"_blank"}
            >
              <YouTubeIcon className="youtube" className="iconSvg" />
            </a>
            <a
              className="links"
              href="https://github.com/RasulovN"
              target={"_blank"}
            >
              <GitHubIcon className="iconSvg" />
            </a>
            <a
              className="links"
              href="https://instagram.com/rasulov_n71?utm_medium=copy_link"
              target={"_blank"}
            >
              <InstagramIcon className="iconSvg" />
            </a>
            <a
              className="links"
              target={"_blank"}
              href="https://t.me//rasulov_n7"
            >
              <TelegramIcon className="iconSvg" />
            </a>
            <a
              className="links"
              target={"_blank"}
              href="https://t.me//rasulov_n7"
            >
              <YouTube className="iconSvg" />
            </a>
          </div>
          <Typography
            className="adressText"
            variant="subtitle1"
            color="text.secondary"
            component="div"
          >
            <p>
              <b>My email:</b> rasulovnurbek71@gmail.com
            </p>
            <p>
              <b>My Numeber:</b> +998 33 959 59 31
            </p>
            <p>
              <b> Address:</b> Karshi city of Kashkadarya region
            </p>
          </Typography>
        </div>
      </Card>
      <div className="card2 container">
        <div className="row">
          <form className="form container" onSubmit={handleSubmit}>
            <h1>Contact Us 🤳</h1>

            <label className="labels">Name</label>
            <TextField
              className="inps"
              id="mui-theme-provider-outlined-input"
              label="Name"
              variant="outlined"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <label className="labels">Email</label>
            <TextField
              className="inps"
              value="email"
              id="mui-theme-provider-outlined-input"
              label="Name"
              variant="outlined"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />

            <label className="labels">Message</label>
            <textarea
              variant="outlined"
              id="custom-css-outlined-input"
              className="inps"
              placeholder="Message"
              value={message}
              onChange={(e) => setMessage(e.target.value)}
            >
              {" "}
            </textarea>

            <div className="col">
              <Button
                variant="contained"
                type="submit"
                style={{ background: loader ? "#ccc" : " rgb(2, 2, 110)" }}
                className="buttons"
                endIcon={<Icon>send</Icon>}
              >
                {" "}
                Send
              </Button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
