import React, { useState, useEffect } from "react";
import db from "../firebase/config";
import { Link } from "react-router-dom";
import YouTubeIcon from "@material-ui/icons/Facebook";
import GitHubIcon from "@material-ui/icons/GitHub";
import TelegramIcon from "@material-ui/icons/Telegram";
import InstagramIcon from "@material-ui/icons/Instagram";
// import FacebookIcon from '@mui/icons-material/Facebook';
import Reveal from "react-reveal/Reveal";
import Typical from "react-typical";

const AboutInfo = () => {
  const [portfolios, setPortfolios] = useState([]);

  useEffect(() => {
    db.collection("portfolio").onSnapshot((snapshot) =>
      setPortfolios(snapshot.docs.map((doc) => doc.data()))
    );
  }, []);

  return (
    <div className="aboutinfo">
      {portfolios.map((portfolio) => (
        <>
          <div className="side2">
            <div className="profile-picture">
              <div className="profile-picture-backgroud"></div>
            </div>
            {/* <img 
                        className='myImage' 
                        src={portfolio.image} 
                        alt="my image"/> */}
          </div>
          <div className="side1">
            <Reveal effect="fadeInUp" duration="1250">
              <h1 className="title">{portfolio.title}</h1>
              <p className="description">{portfolio.description}</p>
            </Reveal>
            <div className="typetext">
              <span className="primary-text">
                {" "}
                <h1>
                  {" "}
                  <Typical
                    loop={Infinity}
                    steps={[
                      "Ethusiastic Dev 😎",
                      1000,
                      "Javascript Dev 🖥",
                      1000,
                      "Creative Desinger",
                      1000,
                      "Full Stack Developer 💻",
                      1000,
                      "React/React Dev 🌐",
                      1000,
                    ]}
                  />
                </h1>
              </span>
            </div>
            <div className="buttons">
              <Link className="linkBtn" to="/about">
                About Me
              </Link>
              <Link className="linkBtn" to="/portfolio">
                Portfolio
              </Link>
            </div>
            <div className="socialNet">
              <a
                className="links"
                href="https://www.facebook.com/nurbek.rasulov.370"
                target={"_blank"}
              >
                <YouTubeIcon className="youtube" className="iconSvg" />
              </a>
              <a
                className="links"
                href="https://github.com/RasulovN"
                target={"_blank"}
              >
                <GitHubIcon className="iconSvg" />
              </a>
              <a
                className="links"
                href="https://instagram.com/rasulov_n71?utm_medium=copy_link"
                target={"_blank"}
              >
                <InstagramIcon className="iconSvg" />
              </a>
              <a
                className="links"
                target={"_blank"}
                href="https://t.me//rasulov_n7"
              >
                <TelegramIcon className="iconSvg" />
              </a>
            </div>
          </div>
        </>
      ))}
    </div>
  );
};

export default AboutInfo;
